class StartDiscussionButton extends React.Component {
	getURI() {

		const topicId = this.props.salesforceTopicId;

		if (topicId) {
			return `https://success.qualys.com/discussions/s/topic/${topicId}/#start-a-discussion`;
		} else {
			return "https://success.qualys.com/discussions/s/#start-a-discussion";
		}

	}
	render () {

		return (
			<a href={this.getURI()}
			   className="q-start-a-discussion q-button-with-arrow">
				<span>Start a discussion</span>
			</a>
		);

	}
}

