class BlogPost extends React.Component {
	render () {

		const post = this.props.data;
		const dateFormatted = moment(post.date).format("LL");
		const postID = `post-${post.ID}`;

		// calculate read time
		// strip HTML tags
		const text = post.content.replace(/(<([^>]+)>)/gi, "");
		// count words
		const wordCount = text.split(" ").filter(n => n !== "").length;
		// estimate 200 words per minute
		const readingTime = Math.ceil(wordCount / 200);

		// get first category from list
		const category = post.categories[Object.keys(post.categories)[0]];
		category.URL = `https://blog.qualys.com/${category.slug}`;

		// get like count
		// TODO and not obvious

		return (
			<article id={postID} className="q-post__article">
				<div className="q-post__article-container">
					<header className="entry-header q-post__entry-header">
						<div className="q-post__entry-avatar">
							<img src={post.author.avatar_URL}
							     width="54"
							     alt={post.author.name} />
						</div>
						<div className="entry-meta q-post__entry-meta">
							<div className="q-post__entry-author">
								<span className="byline">
									<span className="author vcard">
										<a className="url fn n"
										   href={post.author.URL}>
										   {post.author.name}
										</a>
									</span>
								</span>
							</div>
							<div className="q-post__entry-time">
								<span className="posted-on">
									<a href={post.URL}
									   rel="bookmark">
										<time className="entry-date published"
										      dateTime={post.date}>
											{dateFormatted}
										</time>
									</a>
								</span>
								&nbsp;- {readingTime} min read
							</div>
						</div>
					</header>
					<div className="entry-content q-post__entry-content">
						<h3 className="entry-title q-post__entry-title">
							<a href={post.URL}
							   rel="bookmark"
							   dangerouslySetInnerHTML={{__html: post.title}}>
							</a>
						</h3>
					</div>
					<footer className="entry-footer q-post__entry-footer">
						<span className="cat-links">
							Posted in <a href={category.URL}
							   rel="category tag">
								{category.name}
							</a>
						</span>
					</footer>
				</div>
			</article>
		);

	}
}

