const severityLevelMap = {
	"5": "Urgent",
	"4": "Critical",
	"3": "Serious",
	"2": "Medium",
	"1": "Minimal"
};

/*
 * Shows Recent Detections Under Investigation
 * a.k.a. Detections In Progress
 */
class RecentQIDs extends React.Component {

	render () {

		const qids = this.props.data;
		const channel = this.props.channel;

		// only show this component on the VM and VMDR channel page
		const channelsWithRecentQIDs = [
			"vulnerability-management",
			"vmdr"
		];
		if (channel && !channelsWithRecentQIDs.includes(channel.urlSlug)) {
			return null;
		}

		let base = (channel) ? "../" : "";
		let viewAllLink = `${base}vulnerability-detection-pipeline/`;

		const rows = qids.map((qid, index) => {

			qid.href = `${base}vulnerability-detection-pipeline/#qid-${qid.qid}`;

			const cve = qid.cve && qid.cve.length ? qid.cve[0] : `QID-${qid.qid}`;
			let cveTitle = "";
			let cvePlus = "";
			if (qid.cve && qid.cve.length > 1) {
				cveTitle = qid.cve.join(", ");
				cvePlus = "+";
			}
			const severityTitle = `Severity Level 5: ${severityLevelMap[qid.severity]}`;

			return (
				<div key={index} className="q-top-qid">
					<a href={qid.href} title={`View QID ${qid.qid}`}>
						<div className="q-top-qid__header">
							<div className="q-top-qid__cve" title={cveTitle}>
								{cve}{cvePlus}
							</div>
							<div className="severity-gauge__container">
								<span className={`severity-gauge severity-${qid.severity}`}
								      title={severityTitle}></span>
							</div>
						</div>
						<div className="q-top-qid__title">
							{qid.title}
						</div>
						<a href={qid.href}
						   className="q-link-with-arrow nowrap"
						   title={`View QID ${qid.qid}`}>More</a>
					</a>
				</div>
			);

		});

		return (
			<div className="q-top-qids-in-progress">
				<h2>
					<span>Vulnerability Detection Pipeline</span>
					<a href={viewAllLink}
					   className="q-link-with-arrow nowrap"
					   title="View all Detections Under Investigation">View all</a>
				</h2>
				<div className="q-top-qids-in-progress__container">
					{rows}
				</div>
			</div>
		);

	}

}

/*
 * Sharable component for fixed sized boxes that load more content
 * like, What's New, Trending Topics, and Top Articles
 */
class LoadMoreComponent extends React.Component {
	constructor (props) {

		super(props);
		this.size = 3;
		this.state = {
			content: props.content,
			isLoading: false
		};
		this.container = React.createRef();
		this.handleClick = this.handleClick.bind(this);

	}
	componentDidUpdate () {

		let element = this.container.current;
		let firstElementInLastSet = element.querySelector(`*:nth-last-child(${this.size})`);
		if (firstElementInLastSet) {

			let top = firstElementInLastSet.offsetTop;
			if (top !== element.scrollTop) {

				let options = {
					top: top,
					behavior: "smooth"
				};
				element.scrollTo(options);

			}

		}

	}
	handleClick () {

		this.setState({ isLoading: true });

		// set max-height and overflow control
		let element = this.container.current;
		element.style.maxHeight = `${element.offsetHeight}px`;
		element.style.overflowY = "auto";

		// save current scrollHeight for later
		this.lastScrollHeight = element.scrollHeight;

		let component = this;
		this.loadMore().then(content => {

			return component.setState({
				content: content,
				isLoading: false
			});

		});

	}
}


class WhatsNew extends LoadMoreComponent {
	constructor (props) {
		super(props);
		this.state = {
			content: props.content,
		};
	}
	loadMore () {

		let options = {
			limit: this.state.content.items.length + this.size,
		};
		if (this.props.channel && this.props.channel.tags) {
			options.tags = this.props.channel.tags;
		}
		return fetchContent.fetchWhatsNew(options);

	}
	render() {

		const { content, isLoading } = this.state;
		const rows = content.items.map(entry => {
			return (
				<WhatsNewRow data={entry} key={entry.sys.id} />
			);
		});

		const buttonContent = !isLoading ? "Load more" : ". . .";
		let button = "";
		if (content.total >= content.limit) {
			button = <button onClick={!isLoading ? this.handleClick : null}
			                 disabled={isLoading}
			                 className="q-load-more q-button-with-arrow">
				<span>{buttonContent}</span>
			</button>;
		}

		return (
			<div className="q-whats-new">
				<div>
					<h3>
						What’s New
					</h3>
					<div className="q-whats-new__container" ref={this.container}>
						{rows}
					</div>
				</div>
				{button}
			</div>
		);

	}
}

