class RecentContent extends React.Component {
	constructor(props) {

		super(props);
		this.state = {
			content: props.content,
			isLoading: false
		};
		this.handleClick = this.handleClick.bind(this);

	}
	handleClick () {

		this.setState({ isLoading: true });

		let topicId;
		if (this.props.channel) {
			topicId = this.props.channel.salesforceTopicId;
		}
		fetchContent.fetchRecentContent({
			topicId: topicId,
			count: this.state.content.elements.length + 10,
		}).then(content => this.setState({ content: content, isLoading: false }));

	}
	render () {

		const { content, isLoading } = this.state;
		const rows = content.elements.map(c => {
			return (
				<ContentRow data={c} key={c.Id} />
			);
		});

		let viewAllLink = "https://success.qualys.com/discussions/s/";
		if (this.props.channel) {

			// create channel specific view all link
			const topicId = this.props.channel.salesforceTopicId;
			viewAllLink = `https://success.qualys.com/discussions/s/topic/${topicId}`;

		}

		const buttonContent = !isLoading ? "Load more" : ". . .";
		let button = "";
		if (content.nextPageToken) {
			button = <button onClick={!isLoading ? this.handleClick : null}
			                 disabled={isLoading}
			                 className="q-load-more q-button-with-arrow">
				<span>{buttonContent}</span>
			</button>;
		}

		return (
			<div className="q-recent-content">
				<h2>
					<span>Recent Discussions</span>
					<a href={viewAllLink}
					   className="q-link-with-arrow nowrap"
					   title="View all discussions">View all</a>
				</h2>
				<div className="q-recent-item__container">
					{rows}
				</div>
				{button}
			</div>
		);

	}
}

class ContentRow extends React.Component {
	render() {

		const width = 48;
		const d = this.props.data;
		let dateRelative = moment(d.createdDate).fromNow();
		if (typeof process !== "undefined" && moment(d.createdDate).isAfter(moment().subtract(1, "day"))) {

			/*
			 * limit how specific the dateRelative property is during the build.
			 * if it's too specific, then the builds get stuck in an infinite
			 * loop, so just say "moments ago". that is nice because it's similar
			 * in length and still reads well before the client-side code
			 * re-renders with the exact value.
			 */
			dateRelative = "moments ago";

		}
		const topic = d.capabilities.topics.items[0];
		const post = {
			author: {
				avatar: {
					src: `${d.actor.photo.standardEmailPhotoUrl}`,
					href: `https://success.qualys.com/discussions/s/profile/${d.actor.id}`,
					size: width
				},
				href: `https://success.qualys.com/discussions/s/profile/${d.actor.id}`,
				name: `${d.actor.communityNickname}`
			},
			dateRelative: dateRelative,
			href: `https://success.qualys.com/discussions/s/question/${d.id}`,
			lastActivityDate: d.createdDate,
			lastActivityDateFormatted: moment(d.createdDate).format("LLL"),
			subject: d.capabilities.questionAndAnswers.questionTitle,
			space: {
				href: `https://success.qualys.com/discussions/s/topic/${topic.id}`,
				id: topic.id,
				name: topic.name
			}
		};

		return (
			<div className="q-recent-item">
				<span className="q-recent-avatar__container">
					<a href={post.author.href}
					   title={post.author.name}>
						<img src={transparentImage}
						     data-src={post.author.avatar.src}
						     alt={post.author.name}
						     className="lazyload"
						     height={post.author.avatar.size}
						     width={post.author.avatar.size} />
					</a>
				</span>
				<div className="q-recent-item__container">
					<a href={post.href} className="q-recent-subject q-heading-4">{post.subject}</a>
					<div className="q-recent-item__metadata">
						<time className="q-recent-date"
							  dateTime={post.lastActivityDate}
							  title={post.lastActivityDateFormatted}>
							{post.dateRelative}
						</time>
						<span className="q-recent-space">
							&nbsp;in&nbsp;
							<a href={post.space.href}>{post.space.name}</a>
						</span>
						<span className="q-recent-byline__container">
							&nbsp;by&nbsp;
							<a href={post.author.href}>{post.author.name}</a>
						</span>
					</div>
				</div>
			</div>
		);
	}
}


/*
 * note, Jive API does not tell us if there are more results to load
 */
class TrendingContent extends LoadMoreComponent {
	constructor (props) {

		super(props);
		this.state = {
			content: props.content,
		};

	}
	loadMore () {

		let options = {
			count: this.state.content.elements.length + this.size,
		};
		if (this.props.channel) {
			options.topicId = this.props.channel.salesforceTopicId;
		}
		return fetchContent.fetchTrendingContent(options);

	}
	render () {

		const { content, isLoading } = this.state;

		const rows = content.elements.map(c => {
			return (
				<TrendingContentRow data={c} key={c.id} />
			);
		});

		let buttonContent = !isLoading ? "Load more" : ". . .";

		return (
			<div className="q-trending-content">
				<div>
					<h3>
						Trending Topics
					</h3>
					<div className="q-trending-content__container" ref={this.container}>
						{rows}
					</div>
				</div>
				<button onClick={!isLoading ? this.handleClick : null}
				        disabled={isLoading}
				        className="q-load-more q-button-with-arrow">
					<span>{buttonContent}</span>
				</button>
			</div>
		);

	}
}

class TrendingContentRow extends React.Component {
	render() {

		const d = this.props.data;
		const post = {
			href: `https://success.qualys.com/discussions/s/question/${d.id}`,
			subject: d.capabilities.questionAndAnswers.questionTitle,
			date: moment(d.createdDate).format("YYYY-MM-DD"),
			dateFormatted: moment(d.createdDate).format("LL")
		};

		return (
			<div className="q-trending-item">
				<div className="q-trending-item__container">
					<a href={post.href} className="q-recent-subject q-heading-4">{post.subject}</a>
					<div className="q-trending-item__metadata">
						<time dateTime={post.date}>
							{post.dateFormatted}
						</time>
					</div>
				</div>
			</div>
		);

	}
}


if (typeof module !== "undefined") {
	module.exports = {
		ChannelList: ChannelList,
		Homepage: Homepage
	};
}
